.o_horizontal-section.expand .expandable .o_primary-treatment .container {
    padding-bottom: 0;
}

.o_primary-treatment {
    margin: 2rem 0 1rem;
    // Boxes immediately after other boxes
    & + & {
        margin: 1rem 0;
    }
    // The item immediately after
    & + *:not(.o_primary-treatment) {
        margin-top: 2rem;
    }

    .expander {
        font-weight: 600;
        transition-property: background, transform;
        transition-duration: .3s;
        z-index: 5;
        position: relative;
        background: $grey-md-lt url($chevron-grey-dk) right 1rem center / 1.25rem no-repeat;
        padding: 1rem 4rem 1rem 1rem;
        float: none;

        &:hover, &:focus {
            background: $grey-lt url($chevron-grey-dk) right 1rem center / 1.25rem no-repeat;
        }
    }
    // The title of the expanded area
    // And the expander
    .row:first-child {
        .col-md-8 {
            float: none;
        }
    }

    .title {
        transition: .2s;
    }

    .expandable {
        top: 0;
        position: relative;
        z-index: 0;
    }

    &.expanded {
        background-color: $grey-lt;

        .expander {
            transform: scaleY(-1);
            background-color: transparent;
        }

        .title {
            opacity: 0;
        }

        .expandable {
            .row {
                // The content in the expanded area
                &:last-child {
                    padding-bottom: 2rem;
                }
            }
        }
    }

    &.collapsed {
        .title {
            opacity: 1;
        }
    }

    h3 {
        font-size: $font-xl;
        padding-right: 3em;
        padding-top: 0;
        margin-bottom: .75em;
        margin-top: .5em;
        line-height: 1.25;
    }

    h4 {
        font-size: $font-l;
    }

    h5 {
        font-size: $font-m;
        margin-bottom: .25em;
        margin-top: .5em;
        font-weight: 600;
    }

    dl {
        margin-top: 1.5rem;
    }

    dt {
        font-weight: 600;

        &:not(:first-of-type) {
            margin-top: 1.5rem;
        }
    }

    .m_obs {
        padding: 2rem;
        margin-top: 3.5rem;
        margin-bottom: 1.5rem;
    }

    .more {
        &[href*="//"] {
            @include simple-arrow(diagonal, small, after);
        }
        
        @include simple-arrow(right, small, after);
        
    }
   

    .o_timeline {
        & > ol {
            &::after {
                border-top-color: $grey-lt;
            }
        }

        .title {
            &::after {
                background-color: $grey-lt;
            }
        }

        .step-title {
            &::after  {
                border-color: $grey-lt;
            }
        }
    }

}

@media only screen and (min-width: $one-to-two-col) {

    .o_primary-treatment {
        .m_obs {
            background: $yellow url($obs-black) 2rem 2rem / 3rem no-repeat;
            padding: 2rem 2rem 2rem 7rem;

            h4 {
                font-size: $font-l;
                margin-top: .25rem;
            }
        }
    }

}

@media only screen and (min-width: $two-to-three-col) {
    .o_primary-treatment {
        dl {
            margin-top: 0;
        }
    }
}

@media only screen and (min-width: $three-to-four-col) {

    .o_primary-treatment {
        .o_timeline {

            // The horizontal line at the bottom of the timeline
            & > ol {
                padding-left: 1.5rem;

                &::after {
                    left: .75rem;
                    margin-left: 1px;
                }
            }

            // Title and even list items:
            // Content on the right side
            & > ol > li:nth-child(even), & > ol > li:nth-child(odd) {
                margin-left: 0;
                margin-right: 0;
                padding: 0 0 3.5rem 4rem;

                // The line
                &::before {
                    left: 0;
                    right: auto;
                }

                .step-title {
                    &::after {
                        left: -4.75rem;
                        right: auto;
                    }
                }
            }
        }
    }

}